.main-navigation__list {
  display: flex;
  flex-wrap: wrap;
  min-width: 100%;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
    min-width: 440px;
    min-height: 36px;
    padding-top: 18px;
    margin-left: 94px;
  }

  @media (min-width: @desktop-width) {
    position: relative;
    min-width: 478px;
    margin-left: 570px;
  }
}

.main-navigation__item {
  position: relative;

  @media (max-width: @mobile-only-width) {
    width: 280px;
    text-align: center;
    border-top: 1px solid @third-gray;
  }

  @media (min-width: @tablet-width) {
    padding-bottom: 10px;
  }

  @media (min-width: @desktop-width) {
    z-index: 1;
    padding-bottom: 7px;
  }
}

.main-navigation__link {
  .title();
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  padding: 22px 10px;


  @media (min-width: @tablet-width) {
    .title();
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    font-size: 20px;
    line-height: 30px;
  }

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.main-navigation__link--index {
  @media (min-width: @desktop-width) {
    color: @white;
  }

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.main-navigation__link--current::after {
  @media (min-width: @tablet-width) {
    position: absolute;
    content: "";
    left: 0;
    bottom: 3px;
    width: 100%;
    height: 2px;
    background: @basic-green;
  }

  @media (min-width: @desktop-width) {
    bottom: 0;
  }
}

.main-navigation__link--index.main-navigation__link--current::after {
  @media (min-width: @desktop-width) {
    background: @white;
  }
}

.main-navigation__toggle {
  position: absolute;
  top: 22px;
  right: 23px;
  z-index: 2;
  width: 20px;
  height: 20px;
  background-color: @white;
  cursor: pointer;
  border: none;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

.main-navigation--opened {
  width: 100%;

  @media (max-width: @mobile-only-width) {
    position: absolute;
    background: @white;
    z-index: 2;
    top: 65px;
    left: 0;
  }
}

.main-navigation--opened .main-navigation__item {
  @media (max-width: @mobile-only-width) {
    width: 100%;
  }
}

.main-navigation--opened .main-navigation__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: -54px;
    left: -3px;
    width: 25px;
    height: 2px;
    background-color: @black;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}

.main-navigation--closed .main-navigation__list {
  @media (max-width: @mobile-only-width) {
    display: none;
  }
}

.main-navigation--closed .main-navigation__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: @black;
    box-shadow: 0 8px 0 0 @black, 0 16px 0 0 @black;
  }
}

.main-navigation--opened.main-navigation--nojs {
  @media (max-width: @mobile-only-width) {
    display: block;
    position: static;
  }
}

.main-navigation--nojs .main-navigation__toggle {
  display: none;
}
