@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0 auto;
  background: #ffffff;
  font-family: "Lato", "Tahoma", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
@media (min-width: 1280px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}
.body-container {
  margin: 0 auto;
  min-height: 100vh;
  width: 100%;
  background-color: #FFFFFF;
}
.wrapper {
  width: 320px;
  max-width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper {
    width: 768px;
    padding: 0 30px;
  }
}
@media (min-width: 1280px) {
  .wrapper {
    width: 1280px;
  }
}
p {
  margin: 0;
}
a {
  text-decoration: none;
  display: block;
  text-transform: uppercase;
  color: inherit;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
li {
  margin: 0;
  padding: 0;
}
dl {
  margin: 0;
  padding: 0;
}
fieldset {
  margin: 0;
  padding: 0;
}
textarea {
  width: 100%;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  padding: 10px 14px;
}
@media (min-width: 768px) {
  textarea {
    font-size: 20px;
    line-height: 30px;
    padding: 10px 19px;
  }
}
label {
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 16px;
  line-height: 18px;
  margin-right: 13px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  label {
    font-size: 20px;
    line-height: 30px;
  }
}
input {
  flex-grow: 1;
  padding: 11px 18px;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 16px;
  line-height: 18px;
  border-color: transparent;
  outline: 1px solid #e7e7e7;
}
@media (min-width: 768px) {
  input {
    padding: 11px 20px;
    font-size: 20px;
    line-height: 30px;
  }
}
input:hover {
  outline: 1px solid #cdcdcd;
}
input:focus {
  outline: 2px solid #444444;
}
input:active {
  outline: 1px solid #5EAA2F;
}
input:invalid {
  outline: 2px solid #ff8282;
}
input:disabled {
  opacity: 0.3;
}
.title {
  font-family: "Oswald", "Tahoma", sans-serif;
  color: #000000;
}
.button {
  display: block;
  width: 280px;
  background-color: #68B738;
  color: #FFFFFF;
  font-family: "Oswald", "Tahoma", sans-serif;
  font-size: 14px;
  line-height: 14px;
  text-align: center;
  text-transform: uppercase;
  border: none;
  padding: 7px 44px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .button {
    width: 244px;
    font-size: 20px;
    line-height: 20px;
    padding: 10px 26px;
  }
}
.button:hover,
.button:focus {
  background-color: #5EAA2F;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button--secondary {
  font-size: 16px;
  line-height: 20px;
  background-color: #F2F2F2;
  color: #444444;
}
@media (min-width: 768px) {
  .button--secondary {
    width: 250px;
  }
}
.button--secondary:hover,
.button--secondary:focus {
  background-color: #ebebeb;
}
.button--secondary:active {
  color: rgba(68, 68, 68, 0.3);
}
.order-button {
  min-height: 40px;
  margin-bottom: 14px;
}
@media (min-width: 768px) {
  .order-button {
    margin-bottom: 25px;
