}
.main-navigation__link {
  margin: 0;
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
  font-family: "Oswald", "Tahoma", sans-serif;
  color: #000000;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  padding: 22px 10px;
}
@media (min-width: 768px) {
  .main-navigation__link {
    margin: 0;
    font-size: 24px;
    line-height: 26px;
    font-family: "Oswald", "Tahoma", sans-serif;
    color: #000000;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
    padding: 0;
  }
}
@media (min-width: 1280px) {
  .main-navigation__link {
    font-size: 20px;
    line-height: 30px;
  }
}
.main-navigation__link:hover,
.main-navigation__link:focus {
  opacity: 0.6;
}
.main-navigation__link:active {
  opacity: 0.3;
}
@media (min-width: 1280px) {
  .main-navigation__link--index {
    color: #FFFFFF;
  }
}
.main-navigation__link--index:hover,
.main-navigation__link--index:focus {
  opacity: 0.6;
}
.main-navigation__link--index:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-navigation__link--current::after {
    position: absolute;
    content: "";
    left: 0;
    bottom: 3px;
    width: 100%;
    height: 2px;
    background: #68B738;
  }
}
@media (min-width: 1280px) {
  .main-navigation__link--current::after {
    bottom: 0;
  }
}
@media (min-width: 1280px) {
  .main-navigation__link--index.main-navigation__link--current::after {
    background: #FFFFFF;
  }
}
.main-navigation__toggle {
  position: absolute;
  top: 22px;
  right: 23px;
  z-index: 2;
  width: 20px;
  height: 20px;
  background-color: #FFFFFF;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-navigation__toggle {
    display: none;
  }
}
.main-navigation--opened {
  width: 100%;
}
@media (max-width: 767px) {
  .main-navigation--opened {
    position: absolute;
    background: #FFFFFF;
    z-index: 2;
    top: 65px;
    left: 0;
  }
}
@media (max-width: 767px) {
  .main-navigation--opened .main-navigation__item {
    width: 100%;
  }
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: -54px;
  left: -3px;
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__list {
    display: none;
  }
}
.main-navigation--closed .main-navigation__toggle::before,
.main-navigation--closed .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
@media (max-width: 767px) {
  .main-navigation--opened.main-navigation--nojs {
    display: block;
    position: static;
  }
}
.main-navigation--nojs .main-navigation__toggle {
  display: none;
}
